.loader {
   height: 100vh;
   width: 100vw;
   position: fixed;
   top: 0;
   left: 0;
   opacity: 1;
   background: rgba(0, 0, 0, 0);
   pointer-events: none;
   display: flex;
   justify-content: center;
   // align-items: center;
   padding-top: 200px;
   pointer-events: all !important;

   .ldsRing {
      display: inline-block;
      position: relative;
      width: 80px;
      height: 80px;
   }

   .ldsRing div {
      box-sizing: border-box;
      display: block;
      position: absolute;
      width: 64px;
      height: 64px;
      margin: 8px;
      border: 8px solid rgb(0, 27, 145);
      border-radius: 50%;
      animation: ldsRing 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
      border-color: rgb(0, 27, 145) transparent transparent transparent;
   }

   .ldsRing div:nth-child(1) {
      animation-delay: -0.45s;
   }

   .ldsRing div:nth-child(2) {
      animation-delay: -0.3s;
   }

   .ldsRing div:nth-child(3) {
      animation-delay: -0.15s;
   }
}

@keyframes ldsRing {
   0% {
      transform: rotate(0deg);
   }

   100% {
      transform: rotate(360deg);
   }
}
